body {
    background-color: black; 
    font-family: 'SF Atarian System', sans-serif;
    font-family: 'SF Atarian System Extended', sans-serif;
    font-size: 20px;
}

h1 {
    padding-top: 10px;
    color: brown;
    font-weight: bold;
}

#board-canvas {
    position: relative;
    display: block;
    border: 2.5px solid #49fb35;
    border-radius: 1%;
    box-shadow: 0 0 5px 1px #96cbe7;
}

#playerscore {
    font-size: 40px;
    color: brown;

}

#images {
    width: 20px;
    height: 20px;
}

#score img {
    width: 20px;
    height: 20px;
}

section {
    display: flex;
    justify-content: space-between;
}

#instructions, #score {
    margin: auto;
    padding: 10px;
    line-height: 1;
    color: white;
    border: 2.5px solid #49fb35;
    border-radius: 2%;
    width: 400px;
    text-align: center;
    padding-top: 20px;

    // color: brown;
}

#score {
    width: 350px;
}

#inst {
    color: brown;
    font-size: 25px;
}

li {
    line-height: 1.5;
    text-align: left;
}

footer {
    font-size: 10px;
    text-align: center;

}

a > img {
    width: 30px;
    height: 30px;
    // filter: brightness(2);
    // border: 1px solid white;
    border-radius: 25%;
    background-color: white;
    padding: 3px;
}
